<template>
  <div class="recordlist">
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span></span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!--  -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label=""
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd :viewData="viewData" @getAllData="getAllData" ref="editProcess"></popupadd>
              <MultiPrintSelect :printChoice="printChoice" :printItems="printItems" @changeChoice="changeChoice"></MultiPrintSelect>
              <!-- <v-btn class="primary ml-2" @click="printChoice=!printChoice" v-if="printChoice"></v-btn>
              <v-btn class="primary ml-2" @click="printChoice=!printChoice" v-if="!printChoice"></v-btn> -->
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-title></v-card-title>
          <v-card-text>
            <!--  -->
            <v-data-iterator
              :items="viewData"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead class="title">
                      <tr>
                        <th rowspan="3"></th>
                        <th rowspan="3"></th>
                        <th colspan="4"></th>
                        <th colspan="5"></th>
                        <th colspan="3"></th>
                        <th colspan="7"></th>
                        <th colspan="7"></th>
                        <th colspan="11"></th>
                        <th colspan="5"></th>
                        <th colspan="6"></th>
                        <th colspan="5"></th>
                        <th></th>
                        <th rowspan="3"></th>
                        <th rowspan="3"></th>
                      </tr>
                      <tr>
                        <th rowspan="2"></th>
                        <th colspan="3">GCS</th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2">-</th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th colspan="4"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2">-</th>
                        <th colspan="4"></th>
                        <th colspan="3"></th>
                        <th colspan="4"></th>
                        <th colspan="4"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                        <th rowspan="2"></th>
                      </tr>
                      <tr>
                        <th>E</th>
                        <th>M</th>
                        <th>V</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th>-</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td class="d-flex align-center">
                          <v-checkbox label="" v-model="item.check" v-if="!printChoice" @click.stop="filterPrintItem"></v-checkbox>

                          <v-menu transition="scale-transition" offset-y v-if="printChoice">
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on"></v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="edit(item)">
                                <v-list-item-title></v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="del(item)">
                                <v-list-item-title></v-list-item-title>
                              </v-list-item>
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.ideology}}</td>
                        <td>{{item.e}}</td>
                        <td>{{item.m}}</td>
                        <td>{{item.v}}</td>
                        <td>{{item.heart_rate}}</td>
                        <td>{{item.breath_state}}</td>
                        <td>{{item.breath_state_content}}</td>
                        <td>{{item.breath_sound}}</td>
                        <td>{{item.sputum}}</td>
                        <td>{{item.mucosa}}</td>
                        <td>{{item.occlusal}}</td>
                        <td>{{item.swallowing}}</td>
                        <td>{{item.pee}}</td>
                        <td>{{item.urination}}</td>
                        <td>{{item.intestinal_peristalsis}}</td>
                        <td>{{item.flatulence}}</td>
                        <td>{{item.hard_block}}</td>
                        <td>{{item.hard_block_content}}</td>
                        <td>{{item.defecation_treatment}}</td>
                        <td>{{item.skin_exterior}}</td>
                        <td>{{item.skin_complete}}</td>
                        <td>{{item.skin_complete_content}}</td>
                        <td>{{item.limb_edema1}}</td>
                        <td>{{item.limb_edema2}}</td>
                        <td>{{item.limb_edema3}}</td>
                        <td>{{item.limb_edema4}}</td>
                        <td>{{item.balance1}}</td>
                        <td>{{item.balance2}}</td>
                        <td>{{item.balance3}}</td>
                        <td>{{item.joint1}}</td>
                        <td>{{item.joint2}}</td>
                        <td>{{item.joint3}}</td>
                        <td>{{item.joint4}}</td>
                        <td>{{item.muscle_strength1}}</td>
                        <td>{{item.muscle_strength2}}</td>
                        <td>{{item.muscle_strength3}}</td>
                        <td>{{item.muscle_strength4}}</td>
                        <td>{{item.pain}}</td>
                        <td>{{item.pain_content}}</td>
                        <td>{{item.pain_lv}}</td>
                        <td>{{item.pain_rate}}</td>
                        <td>{{item.painkiller}}</td>
                        <td>{{item.hearing_aid}}</td>
                        <td>{{item.hearing}}</td>
                        <td>{{item.vision}}</td>
                        <td>{{item.expression}}</td>
                        <td>{{item.communication}}</td>
                        <td>{{item.understand}}</td>
                        <td>{{item.oralassessment1}}</td>
                        <td>{{item.oralassessment2}}</td>
                        <td>{{item.oralassessment3}}</td>
                        <td>{{item.oralassessment4}}</td>
                        <td>{{item.oralassessment5}}</td>
                        <td>{{item.badbreath}}</td>
                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{printItems.length}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import MultiPrintSelect from "@/components/MultiPrintSelect";
import popupadd from "@/views/document/RecordAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd, MultiPrintSelect },
  data() {
    return {
      items: [], //
      viewData: [], //
      serachKey: "",
      edititem: {}, //
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      printChoice: true, //
      printItems:[],
    };
  },

  mounted() {
    this.getAllData();
  },

  methods: {
    //
    changeChoice(){
      this.printChoice = !this.printChoice;
      this.items.filter(i=>{
        i.check = "";
      })
      this.printItems = [];
    },
    //
    filterPrintItem(){
      if (this.printItems.length == 5){
        var pop = { msg: "5!!", type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
      }
      this.printItems = this.items.filter(i=>{
        return i.check;
      })
    },
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //
      //this.$store.commit("setLoadingOn"); //on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/record",
          qs.stringify(postData)
        )
        .then(rs => {
          // console.log(rs.data)
          this.items = this.viewData = rs.data.map(i => {
            var newItem = JSON.parse(i.datalist);
            newItem.snkey = i.snkey;
            newItem.check = false;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //off
          this.sortBy();
        });
    },
    sortBy() {
      //name
      this.viewData = this.viewData.sort(function(a, b) {
        return a.date < b.date ? 1 : -1;
      });
    },
    edit(item) {
      //
      this.$refs.editProcess.$emit("fromParent", item);
    },
    del(delData) {
      //
      this.$confirm("?").then(res => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "record",
          info: JSON.stringify(delData)
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/record",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach(key => {
        if (key != "") {
          outputitems = outputitems.filter(item => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    }
  }
};
</script>


<style lang="scss">
.recordlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>