<template>
  <div class="MultiPrintOption">
    <v-dialog v-model="dialog" width="30vw">
      <template v-slot:activator="{ on }">
        <v-btn class="success ml-2" v-on="on" @click="enterProcess" v-show="printItems.length <= 5"></v-btn>
      </template>

      <v-card>
        <v-card-title :class="titleClass" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-alert color="primary" outlined class="mt-2">
            <div class="text-right">
              <v-btn class="primary mr-1" @click="selectAll"></v-btn>
              <v-btn color="primary" outlined @click="noAll"></v-btn>
            </div>

            <v-checkbox label="" v-model="list.record" value="true" @click.stop="objcheck"></v-checkbox>
            <v-checkbox label="/(ADL)" v-model="list.kb" value="true" @click.stop="objcheck"></v-checkbox>
            <v-checkbox label="" v-model="list.fall" value="true" @click.stop="objcheck"></v-checkbox>
            <v-checkbox
              label="Braden"
              v-model="list.braden"
              value="true"
              @click.stop="objcheck"
            ></v-checkbox>
            <v-checkbox
              label="SPMSQ"
              v-model="list.spmsq"
              value="true"
              @click.stop="objcheck"
            ></v-checkbox>
          </v-alert>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn class="primary" @click="printOK" v-show="objcheck()"></v-btn>
        </v-card-actions>
        <!-- <pre>{{printItems}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  props: ["printItems"],
  data() {
    return {
      dialog: false,
      list: {
        record: "",
        kb: "",
        fall: "",
        braden: "",
        spmsq:""
      },
      emptyRules: [v => !!v || ""],
      title: "", //
      titleClass: "", //

      recordItems : [],
      kbItems : [],
      fallItems : [],
      bradenItems : [],
      spmsqItems : [],

      documents : ["record","kb","fall","braden","spmsq"],  //
    };
  },

  mounted() {
    //function (showFromParent) id
    this.$on("childFn", function() {
      this.dialog = true;
      this.enterProcess();
    });
    this.selectAll();
  },

  methods: {
    //
    printOK() {
      this.$confirm("?").then(res => {
        if (res) {
          var printData = {}

          printData['matchDate'] = this.printItems.map(i=>{
            return i.date
          })
          var matchStr = JSON.stringify(printData['matchDate'].sort());
          
          this.documents.forEach(document=>{
            printData[document] = this[document+'Items'].filter(i=>{
            var newItem = JSON.parse(i.datalist);
            return matchStr.includes(newItem.date);
          });
          })  

          printData['choiceDocuments'] = this.list;
          console.log(printData);
          
          // //
          sessionStorage.setItem("printData", JSON.stringify(printData));
          let routeData = this.$router.resolve({
            path: "/print/multidocumentprint"
          });
          window.open(routeData.href, "_blank");
        }
      });
    },
    // .21
    objcheck() {
      if (JSON.stringify(Object.values(this.list)).length > 21) {
        return true;
      } else {
        return false;
      }
    },
    enterProcess(item) {
      //(,)
      this.title = "";
      this.titleClass = "font-weight-black primary lighten-2";

      //,
      this.documents.forEach(document=>{
        this.getItems(document);
      })
    },
    getItems(document) {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //
      //this.$store.commit("setLoadingOn"); //on
      
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/" + document,
          qs.stringify(postData)
        )
        .then(rs => {
          this[document+'Items'] = rs.data;
          // this.$store.commit("setLoadingOff"); //off
        });
    },
    //
    selectAll() {
      for (var i in this.list) {
        this.list[i] = "true";
      }
      this.objcheck();
    },
    //
    noAll() {
      for (var i in this.list) {
        this.list[i] = "";
      }
      this.objcheck();
    }
  }
};
</script>

