<script>
import qs from "qs";

export default {
  props: ["viewData"],
  data() {
    return {
      dialog: false,
      list: {}, //
      oldList: {}, //
      processType: "", //addedit
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || ""],
      sman_pic: this.$store.state.base_url + "/images/sman.png",
      loading:false,
    };
  },


  mounted() {
    //function (showFromParent) id
    this.$on("fromParent", function(item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },
  
  methods: {
    addProcess() {
      //,
      this.processType = "add";
      this.title = "";
      this.titleStyle = "font-weight-black error lighten-2";
      // this.list = {};

      //
      if (this.viewData[0] != undefined  ){
        this.list = { ...this.viewData[0] };
      }
      this.list.snkey = "";
      this.list.date = "";
      this.list.edit_man = "";
    },
    editProcess(item) {
      //,
      this.processType = "edit";
      this.title = "";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //
      this.list = { ...item }; //
    },
    addOK() {
      //
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //
        this.list.u_snkey = this.$store.state.uData.snkey;
        this.list["create_man"] = this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //,,

        var postData = {
          u_snkey : this.list.u_snkey,
          datalist : JSON.stringify(this.list),
          create_man : this.list.create_man
        }

        this.loading = true;
        this.axios
        .post("general/add/" + this.$store.state.databaseName + "/record",qs.stringify(postData))
        .then(rs => {
            console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "", type: true, theme: "error" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = { msg: "!!!!",type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //
        this.list["edit_man"] = this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //,,
         
         var postData = {
          snkey : this.list.snkey,
          datalist : JSON.stringify(this.list),
          edit_man : this.list.edit_man
        }

        this.loading = true;  
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/record",
            qs.stringify(postData)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //,vue
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      }
    }
  }
};
</script>

